<template>
  <div>
    <!--面包屑导航区-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>文章管理</el-breadcrumb-item>
      <el-breadcrumb-item>文章列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!--    卡片视图区域-->
    <el-card>
      <!--      搜索与添加区域-->
      <el-row>
        <el-col :span="7">
          <el-input placeholder="请输入标题" v-model="queryInfo.title" @clear="getArticleList" clearable>
            <el-button slot="append" icon="el-icon-search" @click="getArticleList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--      文章列表区域-->
      <el-table :data="articleList" border stripe>
        <el-table-column type="index" align="center"></el-table-column>
        <el-table-column label="标题" prop="title" align="center"></el-table-column>
        <el-table-column label="发布人" prop="nickname" align="center"></el-table-column>
        <el-table-column label="简略内容" prop="contentLue" align="center"></el-table-column>
        <el-table-column label="所属分类" prop="classification" align="center"></el-table-column>
        <el-table-column label="状态" prop="status" align="center">
          <template scope="scopes">
            <el-tag v-if="scopes.row.status === 1" size="mini" type="success">已通过</el-tag>
            <el-tag v-else-if="scopes.row.status === 0" size="mini" type="info">待通过</el-tag>
            <el-tag v-else size="mini" type="danger">未通过</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <!--            查看详情-->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
            <!--            删除-->
            <el-button type="danger" icon="el-icon-delete" size="mini"
                       @click="removeArticleById(scope.row.id)"></el-button>
            <!--            审核-->
<!--            <el-tooltip effect="dark" content="审核" placement="top" :enterable="false">-->
<!--              <el-button type="warning" icon="el-icon-setting" size="mini" @click="audit(scope.row)"></el-button>-->
<!--            </el-tooltip>-->
          </template>
        </el-table-column>
      </el-table>

      <!--      分页区域-->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.current"
          :page-sizes="[2, 5, 10]"
          :page-size="queryInfo.size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total">
      </el-pagination>
    </el-card>

    <!--修改文章的对话框-->
    <el-dialog
        title="详情"
        :visible.sync="editDialogVisible"
        width="50%"
        @close="editDialogClosed"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="150px">
        <el-form-item label="标题：" prop="title">
          <el-input v-model="editForm.title"></el-input>
        </el-form-item>
        <el-form-item label="内容：" prop="content">
          <!--          富文本编辑器组件-->
          <quill-editor v-model="editForm.content"></quill-editor>
        </el-form-item>
        <el-form-item label="分类：" prop="classificationIds">
          <el-select v-model="editForm.classificationIds" multiple placeholder="请选择">
            <el-option
                v-for="item in catelist"
                :key="item.id"
                :label="item.name"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="editArticleInfo">确 定</el-button>
  </span>
    </el-dialog>

    <!--审核的对话框-->
<!--    <el-dialog-->
<!--        title="审核"-->
<!--        :visible.sync="auditDialogVisible"-->
<!--        width="50%"-->
<!--        @close="auditDialogVisible = false"-->
<!--    >-->
<!--      <el-form :model="auditInfo" ref="auditInfoRef" label-width="100px">-->
<!--        <el-form-item label="审核：">-->
<!--          <el-select v-model="auditInfo" placeholder="请选择">-->
<!--            <el-option-->
<!--                v-for="item in rolesList"-->
<!--                :key="item.id"-->
<!--                :label="item.roleName"-->
<!--                :value="item.id">-->
<!--            </el-option>-->
<!--          </el-select>-->
<!--        </el-form-item>-->
<!--      </el-form>-->
<!--      <span slot="footer" class="dialog-footer">-->
<!--    <el-button @click="auditDialogVisible = false">取 消</el-button>-->
<!--    <el-button type="primary" @click="savedRoleInfo">确 定</el-button>-->
<!--  </span>-->
<!--    </el-dialog>-->
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      queryInfo: {
        content: '',
        title: '',
        current: 1,
        size: 5
      },
      total: 0,
      articleList: [],
      classification: [],

      editDialogVisible: false,//显示与隐藏修改用户对话框
      editForm: {},//查询到的用户信息对象
      editFormRules: {
        title: [{
            required: true,
            message: '请输入标题',
            trigger: 'blur'
          }],
        content: [{
            required: true,
            message: '请输入文章内容',
            trigger: 'blur'
          }],
        classificationIds:[{
          required: true,
          message: '请选择所属分类',
          trigger: 'blur'
        }]
      },//修改表单的验证规则对象

      catelist:[],

      auditInfo:{},
      auditDialogVisible:false
    }
  },
  created() {
    this.getArticleList()
    this.getClassification()
  },
  methods: {
    getArticleList() {
      this.$http.get('article', {params: this.queryInfo}).then(res => {
        this.articleList = res.data.data.records
        var contentLue = []
        for (var i = 0; i < this.articleList.length; i++) {
          contentLue[i] = this.articleList[i].content.substr(0, 15)
          this.$set(this.articleList[i], 'contentLue', contentLue[i])
          var re1 = new RegExp("<.+?>", "g");//匹配html标签的正则表达式，"g"是搜索匹配多个符合的内容
          this.articleList[i].contentLue = this.articleList[i].contentLue
              .replace(re1, '')
              .replace(/(<br\s?\/?>)+/gi, '$1')        //去掉重复换行
              .replace(/^<br\/>|<br>/g, '');        //去掉开头的换行;//执行替换成空字符
        }
        //处理所属分类问题
        for (var i = 0; i < this.articleList.length; i++) {
          this.classification[i] = ''
          for (var j = 0; j < this.articleList[i].classificationList.length; j++) {
            this.classification[i] = this.articleList[i].classificationList[j].name + ' ' + this.classification[i];
          }
          this.$set(this.articleList[i], 'classification', this.classification[i])
        }
        //插入一个状态码
        for (var i = 0; i < this.articleList.length; i++) {
          this.$set(this.articleList[i], 'status', 1)
        }
        this.total = res.data.data.total
      }).catch(err => {
        console.log(err)
        this.$message.error('获取文章信息失败')
      })
    },
    //监听 size 改变的事件
    handleSizeChange(newSize) {
      // console.log(newSize)
      this.queryInfo.size = newSize
      this.getArticleList()
    },
    //监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      // console.log(newPage)
      this.queryInfo.current = newPage
      this.getArticleList()
    },
    //详情页面
    showEditDialog(row) {
      this.editForm = row
      this.editDialogVisible = true
    },
    //删除
    async removeArticleById(id) {
      const confirmResult = await this.$confirm('此操作将永久删除该文章，是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonCText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      this.$http.delete('article?id=' + id).then(res => {
        this.$message.success('删除文章成功')
        this.getArticleList()
      }).catch(err => {
        console.log(err)
        this.$message.error('删除文章失败')
      })
    },
    getClassification(){
      this.$http.get('classification').then(res => {
        this.catelist = res.data.data
      }).catch(err => err)
    },
    //监听修改用户对话框的关闭事件
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    //修改用户信息并提交
    editArticleInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        //发起修改用户信息的数据请求
        const { data: res } = await this.$http.put('article', this.editForm)
        if (res.status !== 200) {
          return this.$message.error('更新文章信息失败')
        }
        //关闭对话框
        this.editDialogVisible = false
        //刷新数据列表
        this.getArticleList()
        //提示修改成功
        this.$message.success('更新文章信息成功')
      })
    },
    //审核
    // audit(row){
    //   this.auditInfo = row
    //   this.auditDialogVisible = true
    // }
  }
}
</script>

